import React, { Component } from 'react';
import $ from 'jquery';
import CryptoList from './components/cryptoList';

class App extends Component {
  constructor(){
    super();
    this.state = {
      cryptoList : []
    };
  }

  getCryptoList(){
    $.ajax({
      url: 'https://api.coinmarketcap.com/v2/ticker/?structure=array&convert=INR',
    //  url: 'https://api.coinmarketcap.com/v2/listings/',
      dataType: 'json',
      cache: false,
      success: function(data){
        this.setState({cryptoList: data}, function(){
          console.log(this.state.cryptoList);
        }
        );
      }.bind(this),
      error: function(xhr, status, err){
        console.log(err);
      }
    });
  }

  componentWillMount(){
    this.getCryptoList();
  }

  componentDidMount(){
    this.getCryptoList();
  }

  render() {
    return (
      <div className="App">
       <h1>Welcome to Crypto DB</h1>
       <CryptoList list={this.state.cryptoList.data} />
      </div>
    );
  }
}

export default App;



