import React, { Component } from 'react';
import CryptoListItem from './cryptoListItem';

class CryptoList extends Component {

    render(){
        let CryptoList;
        if(this.props.list){
            CryptoList = this.props.list.map(item => {
                return(
                    <CryptoListItem key={item.id} item={item} />
                );
            });
        }
        return(
            <table>
                <tbody>
                {CryptoList}
                </tbody>
            </table>
        );
    }
}

export default CryptoList;


