import React, { Component } from 'react';

class CryptoListItem extends Component {

    render(){
        return(
            <tr>
                <th><strong>{this.props.item.symbol}</strong> : </th><th>{this.props.item.name} : </th><th>${this.props.item.quotes.USD.price}</th><th>Rs. {this.props.item.quotes.INR.price}</th>
                </tr>
        );
    }
}

export default CryptoListItem;


